<template>
  <!-- 轮播 -->
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <div class="swiper-slide"
           v-for="(item,index) in swiperImg"
           :key="index">
        <img :src="baseImgUrl + item.image">
      </div>
    </div>
    <div class="swiper-pagination"></div>
  </div>
</template>

<script>
import Swiper from 'swiper'
import 'swiper/dist/css/swiper.min.css'
export default {
  props: {
    swiperImg: Array
  },
  data () {
    return {
      baseImgUrl: 'http://localhost:5000/images/'
    }
  },
  methods: {
    initSwiper () {
      new Swiper('.swiper-container', {
        autoplay: true,
        loop: true,
        pagination: {
          el: '.swiper-pagination',
          type: 'bullets',
          clickable: true
        },
      })
    }
  },
  watch: {
    swiperImg (value) {
      this.$nextTick(() => {
        this.initSwiper()
      })
    }
  }
}
</script>

<style lang="scss">
.swiper-container {
  img {
    width: 100%;
    height: 100px;
    border-radius: 5px;
  }
  .swiper-pagination-bullet-active {
    width: 15px;
    border-radius: 15%;
    background-color: #fff;
  }
}
</style>