<template>
  <div class="home-container">
    <div class="header">
      <div class="iconfont icon-dizhi address">
        <div class="location">广东省东莞市常平镇东深路骏马花园</div>
        <div class="iconfont icon-xiala"></div>
      </div>
      <div class="iconfont icon-saomiao scan"> 扫描</div>
    </div>
    <div class="search">
      <!-- <div class="iconfont icon-sousuo"></div> -->
      <input type="text" class="iconfont"
             placeholder="  搜索商家、商品名称">
    </div>
    <!-- 轮播 -->
    <Swiper :swiperImg="swiperImg" />

    <!-- 甄选推荐 -->
    <div class="special-rec">
      <h3>甄选推荐</h3>
      <ul>
        <li>
          <img src="./images/甄选推荐1.png" alt="">
          <span class="title">开春人气美食盘点</span>
          <span class="title-remark">好吃的都在这</span>
        </li>
        <li>
          <img src="./images/甄选推荐2.png" alt="">
          <span class="title">身边老店出新菜</span>
          <span class="title-remark">新菜挖掘机</span>
        </li>
      </ul>
    </div>
    

    <!-- 推荐商家 -->
    <div class="shoplist">
      <h3>推荐商家</h3>
    </div>

  </div>
</template>

<script>
import Swiper from '../../components/Swiper/Swiper.vue'
import {
  getSwiper
} from '../../api/index.js'
export default {
  data () {
    return {
      swiperImg: []
    }
  },
  methods: {
    autoFix (element, top) {
      let H = 0
      let Y = element
      while (Y) {
        H += element.offsetTop
        Y = Y.offsetParent
      }
      window.onscroll = function () {
        var s = document.body.scrollTop || document.documentElement.scrollTop
        if (s + top > H) {
          element.style = "position:fixed;top:0;left:0;right:0;padding:10px 5px 0 5px;margin: 0 auto;"
        } else {
          element.style = ""
        }
      }
    },
    getSwiper () {
      getSwiper().then(data => {
        if (data.status === 0) {
          this.swiperImg = data.swiperImg
        }
      })
    }
  },
  components: {
    Swiper
  },
  created () {
    this.getSwiper()
  },
  mounted () {
    this.autoFix(document.querySelector(".search"), 37)
  }
}
</script>

